html,
body {
  margin: 0;
}

.myContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#quote-box {
  background: whitesmoke;
  width: 50vw;
  height: 40vh;
  border-radius: 10px;
  text-align: center;
  position: relative;
}

/* quote styling */

#text {
  margin-top: 5em;
}

/* Button styling */
#tweet-quote {
  position: absolute;
  bottom: 1em;
  left: 1em;
}

#new-quote {
  position: absolute;
  bottom: 1em;
  right: 1em;
}
#author {
  position: absolute;
  right: 2em;
}

@media only screen and (max-width: 412px) {
  #quote-box {
    background: whitesmoke;
    width: 80vw;
    height: 50vh;
    border-radius: 10px;
    text-align: center;
    position: relative;
  }

  #new-quote,
  #tweet-quote {
    font-size: 10px;
    width: 10x;
    color: blue;
  }
}
